import { getCourseDetail, submitProblem, getCourseQrCode, getProblemHistory } from '@/api/course'
import questionList from '@/config/question.json'
import { getOptionsBatch } from '@/api/common'
import config from "@/config/common.config"
export default {
  data() {
    return{
      courseData:{},
      form:{},
      questionActive:null, //当前显示题目的id
      questionIndex:1, //当前显示的题目index
      trainStatus:1,
      isBiunique:this.$route.params.id === 'undefined', //一对一
      questionList:[],
      imgCode:null,
      config,
      subjects:[],
      showPicker:false,
      activeFormConfig:{}
    }
  },
  computed:{
    columns() {
      return this.activeFormConfig.ans || []
    }
  },
  async mounted() {
    const idWraps = ['q2', 'q5', 'q6']
    this.questionList = questionList.filter(p => idWraps.includes(p.id))
    this.questionActive = this.questionList[0].id
    if (!this.isBiunique) {
      await this.getCourseDetail()
    }
    await this.getCourseQrCode()
  },
  methods: {
    async getCourseDetail() {
      const { data } = await getCourseDetail(this.$route.params.id)
      if (data.code === 0) {
        this.courseData = data.data
        this.getHandleConfig()
      }
    },
    async getCourseQrCode() {
      const {data:{code, data:{courseQrCode}}} = await getCourseQrCode({courseId:this.isBiunique ? 0 : this.$route.params.id})
      if(code === 0){
        this.imgCode = courseQrCode ? `${config.IMG_URL.default}${courseQrCode}` : require('@/assets/images/courseDetail/buyCode.jpg')
      }
    },
    //获取配置
    async getHandleConfig() {
      const { data:{code, data:{subjects}} }= await getOptionsBatch('subjects')
      if (code === 0) {
        let subjectsArr = subjects.filter(p => p.parentId === 0)
        subjectsArr = subjectsArr.map(p =>{
          const children = subjects.filter(item => (item.parentId === p.value))
          return { ...p, children, id:p.value }
        })
        const index = this.questionList.findIndex(p => p.id === 'q5')
        if(index > -1) {
          this.$set(this.questionList, index, {...this.questionList[index], ans:subjectsArr, type:'treeSelect', label:'学科领域'})
        }
        this.subjects = subjects
      }
      //获取答题历史
      const {data:{code:qCode, data:{content}}} = await getProblemHistory()
      if(qCode === 0 && content) {
        const form = JSON.parse(content)
        let q5Str = form['q5']
        let q5Arr = q5Str.indexOf(',') ? q5Str.split(',') : [q5Str]
        let q5 = q5Str.indexOf(',') ? q5Arr[1] : ''
        const q5Item = this.subjects.find(p => (p.value === +q5))
        this.form = {
          q2:form.q2 || '',
          q5:q5Item && q5Item.name ? q5Item.name : '',
          q6:form.q6 || ''
        }
      }
    },
    pickerShow(item) {
      this.activeFormConfig = item
      this.showPicker = true
      // this.$nextTick(() =>{
      //   this.$refs.picker.setColumnValues(2, this.form[item.id])
      // })
    },
    //选择器选中
    onConfirm(value) {
      this.$set(this.form, this.activeFormConfig.id, value[1])
      this.showPicker = false
    },
    async jump(bool) {
      if(bool) {
        const currentTime = parseInt((new Date()).getTime()/1000)
        const videoItem = this.courseData.lives.find(element => {
          return element.scene === 1 || (currentTime<element.end && (currentTime+900)>element.start) || element.liveStatus === 1
        })
        this.$router.push({
          path: videoItem ? `/videoDetail/${videoItem.id}` : `/courseDetail/${this.$route.params.id}`
        })
      } else {
        this.$router.push({
          path: this.isBiunique ? '/studyCenter/biuniqueOrder': '/studyCenter/myOrder'
        })
      }
    },
    async submitQuestion() {
      if (!this.form[this.questionActive]) {
        return this.$toast.fail('请回答问题!')
      }
      if (this.questionIndex === this.questionList.length) {
        const { q2, q5, q6 } = this.form
        const q5Item = this.subjects.find(p => p.name === q5)
        let paramsForm = {
          q2,
          q5:`${q5Item.parentId},${q5Item.value}`,
          q6
        }
        const params = {
          courseId:this.$route.params.id,
          content:JSON.stringify(paramsForm)
        }
        const { data } = await submitProblem(params)
        if(data.code === 0){
          this.$toast.success('提交成功!')
          this.trainStatus = 2
          this.form = {}
        }
      } else {
        this.questionActive = this.questionList[this.questionIndex].id
        this.questionIndex += 1
      }
    }
  }
}
