import CourseCard from '@/components/Common/CourseCard'
import payFinish from '@/components/Common/PayFinish'
import CourseOrder from '@/components/CourseDetail/CourseOrder'
import { getCourseOrder, buyCourse, getCourseMsg, applyFreeCourse } from '@/api/course'
export default {
  components: {
    CourseCard,
    payFinish,
    CourseOrder
  },
  data(){
    return {
      data: {},
      radio: null, //优惠券选择
      isApply:true,
      initData: false,
      inviteCode:'' // 邀请码
    }
  },
  mounted(){
    this.getCourseStatus()
  },
  methods:{
    //获取课程状态
    async getCourseStatus() {
      const { data } = await getCourseMsg(this.$route.params.id)
      if( data.code === 0 ) {
        this.isApply = data.data.apply
        if(!this.isApply) {
          await this.getOrderDetail()
        }
        this.initData = true
      }
    },
    async getOrderDetail() {
      const { data } = await getCourseOrder(this.$route.params.id)
      if(data.code === 0){
        this.data = data.data
        if(this.data.cardList.length> 0) {
          const {card, money} =this.data.cardList.reduce((p, v) => p.money < v.money ? v : p)
          this.radio = card
          this.couponValue = money
        }
      }
    },
    //订单支付跳转
    async buyCourse(fee) {
      let params = {courseId: this.$route.params.id}
      if(this.radio) {
        const index = this.data.cardList.findIndex(p=>p.card === this.radio)
        params= {
          ...params,
          options: { card : this.data.cardList[index].card }
        }
      }
      //添加邀请id
      if(this.$route.query.inviterId){
        params = {
          ...params,
          options:{
            ...params.options,
            inviterId:this.$route.query.inviterId
          }
        }
      }
      //添加邀请码
      if(this.inviteCode){
        params = {
          ...params,
          options:{
            ...params.options,
            inviteCode:this.inviteCode
          }
        }
      }
      //订单金额为0，走报名接口
      if (fee === 0 && !this.radio) {
        const { data } = await applyFreeCourse(this.$route.params.id)
        if (data.code === 0){
          this.$router.push({
            path: `/pay/finish/${this.$route.params.id}`
          })
        }
      } else {
        const { data } = await buyCourse(params)
        if(data.code === 0){
          if (fee === 0) {
            this.$router.push({
              path: `/pay/finish/${this.$route.params.id}`
            })
          }else {
            window.location.href = data.data.payUrl
          }
        }
      }
    }
  },
  head () {
    return {
      title: '订单确认—艾思云课堂'
    }
  }
}
